@import './variables.scss';

.upperBar {  
    &.old {
      background-image: linear-gradient(to bottom, red,orange,yellow,green,blue,indigo,violet);
    }
}

.nameWrapper {
    &.old {
        h1 {
            font-family: 'Courier New', Courier, monospace;
            text-align: left;

            animation: banner 4s linear infinite;
        }
    }
}

.firstname,
.lastname {
    &.old {
        border: 3px dashed red;
        height: 85px;
        width: 300px;
        margin: 0 0 10px 0;
        overflow: hidden;
    
        &::after {
          background-color: white;
          animation: unset;
        }
    }

    @media (min-width: 750px) {
        &.old {
            height: 125px;
            width: 600px;
        }
    }

    @media (min-width: 1100px) {
        &.old {
            height: 200px;
            width: 900px;
        }
    }
}

.lowerBar {
    &.old {
        background-image: unset;

        .lowerContentWrapper:first-child {
            border: 2px solid gray;
            background-color: #EFEFEF;
            width: 135px;
            height: 36px;
        }

        .lowerContentWrapper:last-child {
            width: 60px;
            height: 36px;
        }
    
        .lowerContentWrapper > svg {
            fill: transparent;
            display: none;
        }
    
        .lowerContentWrapper > span {
            padding: 5px 15px;
            font-family: 'Times New Roman', Times, serif;
        }
    
        .lowerContentWrapper > a {
            font-family: 'Times New Roman', Times, serif;
            text-decoration: underline;
            color: blue;
            padding: 0;
        }

        @media (max-width: 750px) {
            padding: 0 35px;
        }
    }
}

@keyframes banner {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-150%);
    }
}