@import './variables.scss';

$startLightNeonShadow: 0px 20px 80px $white;
$fullLightNeonShadow: 0px 20px 80px $white, -40px 10px 50px, 0 0 5px,  0 0 1em $darkerNeonColor, 0 0 0.5em $darkerNeonColor, 0 0 0.1em $darkerNeonColor, 0 10px 3px #000;
$flickerColor: #9d1191;

.imageBackground {
    display: none;

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    > .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $black;
        opacity: 0;
        z-index: 2;
        animation: darken 0.75s ease forwards;
    }

    &.neon {
        display: block;
    }
}

.nameWrapper {
    &.neon {
      color: $black;
      text-shadow: 0px 0px 80px transparent;
      animation: addTextShadow 0.5s 0.5s ease, lightUpNeon 1.5s 1s ease forwards, flicker 3s 3s ease infinite;
  
      svg {
        display: none;
      }
  
      h1 {
        width: fit-content;
        font-family: "Playwrite";
        font-weight: lighter;
        text-transform: lowercase;

        @media (min-width: 1100px) {
            font-size: 110px;
          }
      }

      @media (min-width: 750px) {
        flex-direction: row;
      }
    }
}

.firstname,
.lastname {
    &.neon {
        &::after {
            animation: none;
          }
    }
}

.firstname {
    &.neon {
        width: 108px;

        @media (min-width: 750px) {
            width: 205px;
        }
    }
}

.lastname {
    &.neon {
        width: 240px;

        @media (min-width: 750px) {
            width: 425px;
        }
      }
}

.lowerBar {
    &.neon {
        font-family: "Raleway";
        color: $white;
        flex-wrap: wrap;

        .lowerContentWrapper {
            width: fit-content;
            border: 2px solid $neonColor;
            border-radius: 10px;

            box-shadow: 0 0 1em $darkerNeonColor, 0 0 0.5em $darkerNeonColor, 0 0 0.1em $darkerNeonColor, inset 0 0 1em $darkerNeonColor, inset 0 0 0.5em $darkerNeonColor, inset 0 0 0.1em $darkerNeonColor;

            > svg {
                display: none;
            }

            > a,
            > span {
                position: relative;
                padding: 10px 20px;
                color: $white;
                letter-spacing: 0.8px;
                font-weight: 300;
                text-transform: uppercase;
            }
        }

        @media (max-width: 750px) {
            .lowerContentWrapper > a,
            .lowerContentWrapper > span {
                font-size: 16px;
            }
          }

        @media (min-width: 750px) {
            flex-wrap: nowrap;
        }
    }
}

.credits {
    display: none;

    &.neon {
        display: block;
        order: 3;
        width: 100%;
        text-align: center;
        margin-top: 10px;
        font-size: 14px;
        line-height: 16px;

        a {
            color: $white; 
        }

        @media (min-width: 750px) {
            font-size: 16px;
            line-height: 18px;
            margin-top: 0;
            width: fit-content;
            order: unset;
        }
    }
}

@keyframes darken {
    0%{
        opacity: 0;
    }
    100% {
        opacity: 0.6;
    }
}

@keyframes addTextShadow {
    0% {
        text-shadow: 0px 0px 80px transparent;
    }

    100% {
        text-shadow: 0px 20px 80px $white;
    }
}

@keyframes lightUpNeon {
    0% {
        color: $black;
        text-shadow: $startLightNeonShadow;
    }

    5% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }

    10% {
        color: $black;
        text-shadow: $startLightNeonShadow;
    }

    20% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }

    25% {
        color: $black;
        text-shadow: $startLightNeonShadow;
    }

    100% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }
}

@keyframes flicker {
    0% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }
    60% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }
    70% {
        color: rgba(#ffcbcb, 0.5);
        text-shadow: 0px 20px 80px rgba(#FAFAFA, 0.5), -40px 10px 50px, 0 0 5px,  0 0 1em $flickerColor, 0 0 0.5em $flickerColor, 0 0 0.1em $flickerColor, 0 10px 3px #000;
    }
    80% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }
    92% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }
    95% {
        color: rgba(#ffcbcb, 0.5);
        text-shadow: 0px 20px 80px rgba(#FAFAFA, 0.5), -40px 10px 50px, 0 0 5px,  0 0 1em $flickerColor, 0 0 0.5em $flickerColor, 0 0 0.1em $flickerColor, 0 10px 3px #000;
    }
    98% {
        color: $neonColor;
        text-shadow: $fullLightNeonShadow;
    }
}