@font-face {
  font-family: "Permanent Marker";
  src: local("Permanent Marker"),
   url("../assets/fonts/PermanentMarker-Regular.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Satisfy";
  src: local("Satisfy"),
   url("../assets/fonts/Satisfy-Regular.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Raleway";
  src: local("Raleway"),
   url("../assets/fonts/Raleway-Regular.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: "Raleway";
  src: local("Raleway"),
   url("../assets/fonts/Raleway-Light.ttf") format("truetype");
  font-weight: 300;
}

@font-face {
  font-family: "Playwrite";
  src: local("Playwrite"),
   url("../assets/fonts/PlaywriteAUNSW-ExtraLight.ttf") format("truetype");
  font-weight: lighter;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Permanent Marker', cursive;
  font-size: 18px;

  * {
    box-sizing: border-box;
  }
}

#page-eva {
  width: 100vw;
  height: 100vh;

  overflow: hidden;
}


